import React, { useState } from "react";
import { Button, Table, Modal, Form, Pagination } from "react-bootstrap";
import { FaEdit, FaTrash, FaUndo, FaFileInvoice, FaTags } from "react-icons/fa"; // Icons
import Layout from "./Layout"; // Assume you have a Layout component

const ShippingPage = () => {
  const [orders, setOrders] = useState([
    { id: 1, recipientName: "John Doe", address: "123 Elm St", status: "Ready to Ship", shipmentId: "SHIP001" },
    { id: 2, recipientName: "Jane Smith", address: "456 Oak Ave", status: "Shipped", shipmentId: "SHIP002" },
    { id: 3, recipientName: "Alice Johnson", address: "789 Pine Rd", status: "Ready to Ship", shipmentId: "SHIP003" },
    { id: 4, recipientName: "Bob Brown", address: "101 Maple St", status: "Shipped", shipmentId: "SHIP004" },
    // Add more static data for pagination demonstration
  ]);
  const [showModal, setShowModal] = useState(false);
  const [modalType, setModalType] = useState("");
  const [formData, setFormData] = useState({});
  const [processing, setProcessing] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 3;

  const totalPages = Math.ceil(orders.length / itemsPerPage);

  const paginatedOrders = orders.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleAction = (actionType, shipmentId) => {
    setProcessing(true);
    setTimeout(() => {
      if (actionType === "update") {
        console.log("Shipment details updated for:", shipmentId);
      } else if (actionType === "cancel") {
        console.log("Shipment canceled:", shipmentId);
      } else if (actionType === "return") {
        console.log("Return initiated:", shipmentId);
      }
      setProcessing(false);
      setShowModal(false);
    }, 1000);
  };

  const generateLabelOrInvoice = (type, shipmentId) => {
    console.log(`${type} generated for shipment:`, shipmentId);
    alert(`${type} generated for shipment ID: ${shipmentId}`);
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h1>Shipping Management</h1>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>Order ID</th>
              <th>Recipient Name</th>
              <th>Address</th>
              <th>Status</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginatedOrders.map((order) => (
              <tr key={order.id}>
                <td>{order.id}</td>
                <td>{order.recipientName}</td>
                <td>{order.address}</td>
                <td>{order.status}</td>
                <td>
                  <Button
                    variant="secondary"
                    size="sm"
                    onClick={() => {
                      setModalType("update");
                      setFormData({
                        shipmentId: order.shipmentId,
                        recipientName: order.recipientName,
                        address: order.address,
                      });
                      setShowModal(true);
                    }}
                  >
                    <FaEdit />
                  </Button>{" "}
                  <Button
                    variant="danger"
                    size="sm"
                    onClick={() => handleAction("cancel", order.shipmentId)}
                  >
                    <FaTrash />
                  </Button>{" "}
                  <Button
                    variant="warning"
                    size="sm"
                    onClick={() => handleAction("return", order.shipmentId)}
                  >
                    <FaUndo />
                  </Button>{" "}
                  <Button
                    variant="info"
                    size="sm"
                    onClick={() => generateLabelOrInvoice("Label", order.shipmentId)}
                  >
                    <FaTags />
                  </Button>{" "}
                  <Button
                    variant="success"
                    size="sm"
                    onClick={() => generateLabelOrInvoice("Invoice", order.shipmentId)}
                  >
                    <FaFileInvoice />
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>

        {/* Pagination */}
        <Pagination>
          <Pagination.Prev
            onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
            disabled={currentPage === 1}
          />
          {Array.from({ length: totalPages }, (_, i) => (
            <Pagination.Item
              key={i + 1}
              active={i + 1 === currentPage}
              onClick={() => setCurrentPage(i + 1)}
            >
              {i + 1}
            </Pagination.Item>
          ))}
          <Pagination.Next
            onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
            disabled={currentPage === totalPages}
          />
        </Pagination>

        {/* Modal */}
        <Modal show={showModal} onHide={() => setShowModal(false)}>
          <Modal.Header closeButton>
            <Modal.Title>
              {modalType === "update" && "Update Shipment Details"}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group>
                <Form.Label>Recipient Name</Form.Label>
                <Form.Control
                  type="text"
                  name="recipientName"
                  value={formData.recipientName || ""}
                  onChange={handleFormChange}
                />
              </Form.Group>
              <Form.Group>
                <Form.Label>Address</Form.Label>
                <Form.Control
                  type="text"
                  name="address"
                  value={formData.address || ""}
                  onChange={handleFormChange}
                />
              </Form.Group>
              <Button
                type="button"
                onClick={() => handleAction(modalType, formData.shipmentId)}
                disabled={processing}
              >
                {processing ? "Processing..." : "Save Changes"}
              </Button>
            </Form>
          </Modal.Body>
        </Modal>
      </div>
    </Layout>
  );
};

export default ShippingPage;
